import { Component, NgModule, Injectable } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

@Injectable()
export class Candeactivateguard {
    private isedit = false;
    canDeactivate(component) {

        if (this.isedit) {
            const confirmation = window.confirm('Desea salir sin guardar cambios?');
            if (confirmation) {
                this.isedit = false;
            }
            return confirmation;
        } else { return true; }


    }
    public setedit(isedit) {
        this.isedit = isedit;
    }
}



