import { NgModule, LOCALE_ID } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';

import { AppRoutingModule } from './/app-routing.module';
import { Ng4LoadingSpinnerModule } from 'ng4-loading-spinner';
import { AuthenticationModule, AuthGuard } from './shared';
import { EnvironmentModule } from './shared';
import { HttpModule } from '@angular/http';
import { FormValidationModule } from './shared';
import { NotificationModule } from './shared';
import { LocalDataModule } from './shared';
import { AlertModule } from 'ngx-bootstrap/alert';
import { SweetAlert2Module } from '@toverux/ngx-sweetalert2';
import { SnotifyModule, SnotifyService, ToastDefaults } from 'ng-snotify';
import { TypeaheadModule } from 'ngx-bootstrap/typeahead';
import { DiasWeekModule } from './dias-week/dias-week.module';
//services
import { LoginService } from "./login/login.service";
import { LogoutService } from "./logout/logout.service";
import { UsuariosService } from "./usuarios/usuarios.service";
import { EmpresaService } from "./empresa/empresa.service";
import { ClienteService } from "./clientes/cliente.service";
import { ZonaTecnicaService } from "./zonaTecnica/zona-tecnica.service";
import { TipocompoenenteService } from "./tipocomponente/tipocompoenente.service";
import { TipocontratoService } from "./tipocontrato/tipocontrato.service";
import { EstacionservicioService } from "./estacionservicio/estacionservicio.service";
import { ReportesService } from './reportes/reportes.service';
import { ProyectosService } from './proyectos/proyectos.service';
//components
import { AppComponent } from './app.component';
import { SiteLayoutComponent } from './_layout/site-layout/site-layout.component';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { LogoutComponent } from './logout/logout.component';
import { UsuariosIndexComponent } from './usuarios/usuarios-index/usuarios-index.component';
import { UsuariosCreateComponent } from './usuarios/usuarios-create/usuarios-create.component';
import { UsuariosEditComponent } from './usuarios/usuarios-edit/usuarios-edit.component';
import { EmpresaIndexComponent } from './empresa/empresa-index/empresa-index.component';
import { EmpresaAddComponent } from './empresa/empresa-add/empresa-add.component';
import { EmpresaEditComponent } from './empresa/empresa-edit/empresa-edit.component';
import { ClienteIndexComponent } from './clientes/cliente-index/cliente-index.component';
import { ClienteAddComponent } from './clientes/cliente-add/cliente-add.component';
import { ClienteEditComponent } from './clientes/cliente-edit/cliente-edit.component';
import { ZontaTecnicaIndexComponent } from './zonaTecnica/zonta-tecnica-index/zonta-tecnica-index.component';
import { ZonaTecnicaAddComponent } from './zonaTecnica/zona-tecnica-add/zona-tecnica-add.component';
import { ZonaTecnicaEditComponent } from './zonaTecnica/zona-tecnica-edit/zona-tecnica-edit.component';
import { TipocomponenteIndexComponent } from './tipocomponente/tipocomponente-index/tipocomponente-index.component';
import { TipocomponenteAddComponent } from './tipocomponente/tipocomponente-add/tipocomponente-add.component';
import { TipocomponenteEditComponent } from './tipocomponente/tipocomponente-edit/tipocomponente-edit.component';
import { TipocontratoAddComponent } from './tipocontrato/tipocontrato-add/tipocontrato-add.component';
import { TipocontratoEditComponent } from './tipocontrato/tipocontrato-edit/tipocontrato-edit.component';
import { TipocontratoIndexComponent } from './tipocontrato/tipocontrato-index/tipocontrato-index.component';
import { TipocomponenteAdicionalIndexComponent } from './tipocomponente/tipocomponente-adicional-index/tipocomponente-adicional-index.component';
import { RequestInterceptor } from './shared/interceptor/http_interceptor';
import { IncidenciaService } from './incidencias/incidencia.service';
import { DashboardService } from './dashboard/dashboard.service';
import { DragulaModule } from 'ng2-dragula';
import { AlertasService } from './pages/alertas/alertas.service';
import { Candeactivateguard } from './shared/can-deactivate-guard/can-deactivate-guard.service';
import { MenuperfilService } from './pages/menuperfil/menuperfil.service';
import { UsuariosPerfilComponent } from './pages/usuarios/usuarios-perfil/usuarios-perfil.component';
import { registerLocaleData } from '@angular/common';

import localeEs from '@angular/common/locales/es';
import { ArchivouploadService } from './archivoupload/archivoupload.service';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations'; // this is needed!
import { LocationService } from './shared/location/location.service';
import { AuditoriaService } from './shared/auditoria/auditoria.service';
import { PerfilService } from './services/perfil/perfil.service';
import { DashboardObrasComponent } from './pages/dashboard/dashboard-obras/dashboard-obras.component';
import { DashboardDefaultComponent } from './pages/dashboard/dashboard-default/dashboard-default.component';
import { FichacomercialService } from './services/fichacomercial/fichacomercial.service';
import { FomulariodinamicoService } from './services/fomulariodinamico/fomulariodinamico.service';

import 'datatables.net-buttons/js/buttons.print.js'
import 'datatables.net-buttons/js/buttons.html5.js'

declare var $;
window['$'] = $;

registerLocaleData(localeEs, 'es');
@NgModule({
  declarations: [
    AppComponent,
    SiteLayoutComponent,
    HomeComponent,
    LoginComponent,
    LogoutComponent,
    UsuariosIndexComponent,
    UsuariosCreateComponent,
    UsuariosEditComponent,
    EmpresaIndexComponent,
    EmpresaAddComponent,
    EmpresaEditComponent,
    ClienteIndexComponent,
    ClienteAddComponent,
    ClienteEditComponent,
    ZontaTecnicaIndexComponent,
    ZonaTecnicaAddComponent,
    ZonaTecnicaEditComponent,
    TipocomponenteIndexComponent,
    TipocomponenteAdicionalIndexComponent,
    TipocomponenteAddComponent,
    TipocomponenteEditComponent,
    TipocontratoAddComponent,
    TipocontratoEditComponent,
    TipocontratoIndexComponent,
    TipocomponenteAdicionalIndexComponent,
    UsuariosPerfilComponent,
    DashboardObrasComponent,
    DashboardDefaultComponent,

  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    AppRoutingModule,
    Ng4LoadingSpinnerModule.forRoot(),
    AlertModule.forRoot(),
    SweetAlert2Module.forRoot(),
    SnotifyModule,
    HttpModule,
    AuthenticationModule,
    EnvironmentModule,
    FormValidationModule,
    NotificationModule,
    LocalDataModule,
    TypeaheadModule.forRoot(),
    DiasWeekModule,
    DragulaModule.forRoot(),
    BrowserAnimationsModule,

  ],
  exports: [TypeaheadModule,],
  providers: [{ provide: 'SnotifyToastConfig', useValue: ToastDefaults },
  { provide: LOCALE_ID, useValue: 'es' },
    SnotifyService,
    LoginService,
    LogoutService,
    UsuariosService,
    EmpresaService,
    ClienteService,
    ZonaTecnicaService,
    TipocompoenenteService,
    EstacionservicioService,
    ReportesService,
    ProyectosService,
    TipocontratoService,
    IncidenciaService,
    AlertasService,
    DashboardService,
    Candeactivateguard,
    MenuperfilService,
    ArchivouploadService,
    AuthGuard,
    LocationService,
    AuditoriaService,
    PerfilService,
    FichacomercialService,
    FomulariodinamicoService,
  {
    provide: HTTP_INTERCEPTORS,
    useClass: RequestInterceptor,
    multi: true,
  },
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



