import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { TipocontratoService } from "./../tipocontrato.service";
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from './../../shared';
import { tipocontrato } from '../../viewmodel/tipocontrato';
import { CategoriaIncidente } from '../../viewmodel/categoriaincidente';

@Component({
  selector: 'app-tipocontrato-edit',
  templateUrl: './tipocontrato-edit.component.html',
  styleUrls: ['./tipocontrato-edit.component.css']
})
export class TipocontratoEditComponent implements OnInit {

  item: tipocontrato;
  newcategoriaincidente: CategoriaIncidente;
  constructor(private spinnerService: Ng4LoadingSpinnerService,
    private tipocontratoService: TipocontratoService,
    private router: Router,
    private route: ActivatedRoute,
    private notificationService: NotificationService) { }



  ngOnInit() {
    this.item = new tipocontrato();
    this.getById();
    this.newcategoriaincidente = new CategoriaIncidente();
    this.newcategoriaincidente.dias = "";
  }

  getById() {
    this.spinnerService.show();
    const $id = this.route.snapshot.paramMap.get('id');
    this.tipocontratoService.getById($id)
      .subscribe((response) => {
        this.item = response.data
        this.spinnerService.hide();
      })
  }
  update() {
    this.spinnerService.show();
    this.tipocontratoService.update(this.item)
      .subscribe((value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess('Tipo Contrato Actualziado con Exito.')
        this.router.navigate(['tipocontrato']);
      });
  }
  onvaluechanged(value) {
    if (value.index === undefined || value.index === '') {
      this.newcategoriaincidente.dias = value.dias;
    }else {
      this.item.categoriaincidentes[value.index].dias = value.dias;
    }
  }
  addNewcategoria(ltsdias) {
    this.item.categoriaincidentes.push(this.newcategoriaincidente);
    this.newcategoriaincidente = new CategoriaIncidente();
    this.newcategoriaincidente.dias = "";
    ltsdias.clearlist();
  }
  onradioChange(row, value) {
    row.tipotiempo = value;
  }
  deletecomponent(componente) {
    let index = this.item.categoriaincidentes.indexOf(componente);
    this.item.categoriaincidentes.splice(index, 1);

  }
}



