import { Component, OnInit, AfterViewInit } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from './../../shared';
import { EmpresaService } from "./../empresa.service";
declare var $;

import "datatables.net";
import "datatables.net-bs4";
import "datatables.net-buttons";

@Component({
  selector: 'app-empresa-index',
  templateUrl: './empresa-index.component.html',
  styleUrls: ['./empresa-index.component.css']
})
export class EmpresaIndexComponent implements OnInit, AfterViewInit {

  empresas = [];
  table = null;
  constructor(
    private spinnerService: Ng4LoadingSpinnerService,
    private empresaService: EmpresaService,
    private notificationService: NotificationService
  ) { }

  ngOnInit() {

    this.get();

  }
  get() {
    this.spinnerService.show();
    this.empresaService.get(1)
      .subscribe((response) => {
        this.empresas = response.data;
        this.reInitDatatable();
        this.spinnerService.hide();

      });
  }
  loadtable() {
    let exampleId: any = $('#example23');    
  
    this.table=exampleId.DataTable({
      dom: 'Bfrtip',
      buttons: [
        'copy', 'csv', 'excel', 'pdf', 'print'
    ]
    });
  }
  ngAfterViewInit() {

    

  }
  delete(id) {
    this.spinnerService.show();
    this.empresaService.delete(id)
      .subscribe((value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess('Empresa Eliminada con Exito.')
        this.get();

      });
  }
  private reInitDatatable(): void {
    if (this.table) {
      this.table.destroy()
      this.table = null
    }
    setTimeout(() => this.loadtable(), 0)
  }
}



