import { Component, OnInit, AfterViewInit, ViewChild, OnDestroy } from '@angular/core';
declare var $;

import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from './../../shared';
import { EmpresaService } from "./../../empresa/empresa.service";
import { LocalDataService } from './../../shared';
import { Router } from '@angular/router';
import { user } from '../../viewmodel/user';
import { empresa } from '../../viewmodel/empresa';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { IncidenciaService } from '../../incidencias/incidencia.service';
import { AlertasService } from '../../pages/alertas/alertas.service';
import { ESTADOINCIDENCIAS } from '../../mocks/estadoincidente';
import { Observable, Subscription } from 'rxjs';
import { timer } from 'rxjs/observable/timer';
@Component({
  selector: 'app-site-layout',
  templateUrl: './site-layout.component.html',
  styleUrls: ['./site-layout.component.css']
})

export class SiteLayoutComponent implements OnInit, AfterViewInit, OnDestroy {
  empresas: empresa[];
  empresa: any;
  user: any;
  menu: any;
  incidencias = [];
  estadoincidente = ESTADOINCIDENCIAS;
  alive = true;
  @ViewChild('deleteSwal') private deleteSwal: SwalComponent;
  constructor(private spinnerService: Ng4LoadingSpinnerService,
    private empresaService: EmpresaService,
    private notificationService: NotificationService,
    private localDataService: LocalDataService,
    private router: Router,
    private incidenciaService: AlertasService,

  ) { }

  ngOnInit() {
    this.user = new user();

    this.user = this.localDataService.getLocalData('userInfo');
    this.empresa = this.localDataService.getLocalData('empresaSeleccionada');
    if (this.empresa == null) { this.empresa = new empresa(); }
    if (this.user == null) {
      this.router.navigateByUrl('/logout');
    }
    this.get();
    this.getdata();
    this.subscribeToData();
  }
  ngAfterViewInit() {
    $.getScript('assets/dist/js/custom.js');
    $.getScript('assets/dist/js/dashboard1.js');
    $.getScript('assets/node_modules/icheck/icheck.min.js');
    $.getScript('assets/node_modules/icheck/icheck.init.js');


  }
  validateEmpresa() {
    if (this.empresas.length == 0 || this.empresa.name == undefined) {
      this.deleteSwal.show();

    }
  }
  get() {
    this.spinnerService.show();
    this.empresaService.getByUser(this.user.id)
      .subscribe((response) => {
        this.empresas = response.data.empresas;
        this.menu = response.data.menu.map( m => { m.open = false; return m; } );
        this.localDataService.setmenu(this.menu);
        if (this.empresas.length == 1 && this.empresa.name == undefined) {
          this.onSelect(this.empresas[0]);
        }
        else {
          this.spinnerService.hide();
          this.validateEmpresa();
        }

      });
  }
  onSelect(empresa) {
    this.localDataService.setLocalData('empresaSeleccionada', empresa);

    this.empresa = empresa;
    this.deleteSwal.nativeSwal.close();
    location.reload(true);

  }
  logout() {
    this.router.navigateByUrl('/logout');
  }
  empselect(id) {

    return this.empresa.id == id;
  }
  private subscribeToData(): void {
    timer(0, 40000)
      .takeWhile(() => this.alive) // only fires when component is alive
      .subscribe(() => {
        this.getdata();
      });
  }
  getdata() {
    this.incidenciaService.getbytoken('empresa=' + this.empresa.id + '&estado=INT&buzon=1')
      .subscribe((response) => {

        this.incidencias = response.data.incidencias;

        this.spinnerService.hide();

      });
  }
  getclassestadoincidencia(estado) {
    let result;
    result = this.estadoincidente.filter(estadoinc => estadoinc.value === estado);
    return result.length > 0 ? result[0].btn : '';
  }
  ngOnDestroy() {
    this.alive = false; // switches your IntervalObservable off

  }
  parametros(value) {
    if (value != '') {
      return JSON.parse(value);
    } else {
      return null;
    }

    
  }
  openMenu(menu){
    this.menu = this.menu.map( m => {
      m.open = menu.id == m.id ? m.open : false;
      return m;
    } ); 
    menu.open = !menu.open;
  }
}



