import { Component, OnInit } from '@angular/core';
import { Router , ActivatedRoute } from '@angular/router';
import { ClienteService } from "./../cliente.service";
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from './../../shared';
import { empresa } from '../../viewmodel/empresa';
import { Candeactivateguard } from '../../shared/can-deactivate-guard/can-deactivate-guard.service';

@Component({
  selector: 'app-cliente-edit',
  templateUrl: './cliente-edit.component.html',
  styleUrls: ['./cliente-edit.component.css']
})
export class ClienteEditComponent implements OnInit {

  constructor(
    private spinnerService: Ng4LoadingSpinnerService,
    private clienteService: ClienteService,
    private router: Router,
    private route: ActivatedRoute,
    private notificationService: NotificationService,
    private candeactivateguard: Candeactivateguard
  ) { 
    this.candeactivateguard.setedit(true);
  }
  empresadata:empresa;
  ngOnInit() {
    this.empresadata= new empresa();
    this.getById();
  }

  getById(){
    this.spinnerService.show();
  	const $id = this.route.snapshot.paramMap.get('id');
    this.clienteService.getById($id)
      .subscribe((response) => {
        this.empresadata = response.data
        this.spinnerService.hide();
    })
  }
  update() {
    this.candeactivateguard.setedit(false);
    this.spinnerService.show();
  	this.clienteService.update(this.empresadata)
      .subscribe((value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess('Cliente Actualizado con Exito.')
        this.router.navigate(['clientes']);   
      });
  }
}
 


