import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CommonModule } from '@angular/common';
import { SiteLayoutComponent } from './_layout/site-layout/site-layout.component';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { UsuariosIndexComponent } from './usuarios/usuarios-index/usuarios-index.component';
import { UsuariosCreateComponent } from './usuarios/usuarios-create/usuarios-create.component';
import { UsuariosEditComponent } from './usuarios/usuarios-edit/usuarios-edit.component';
import { EmpresaIndexComponent } from './empresa/empresa-index/empresa-index.component';
import { EmpresaAddComponent } from './empresa/empresa-add/empresa-add.component';
import { EmpresaEditComponent } from './empresa/empresa-edit/empresa-edit.component';
import { ClienteIndexComponent } from './clientes/cliente-index/cliente-index.component';
import { ClienteAddComponent } from './clientes/cliente-add/cliente-add.component';
import { ClienteEditComponent } from './clientes/cliente-edit/cliente-edit.component';
import { ZontaTecnicaIndexComponent } from './zonaTecnica/zonta-tecnica-index/zonta-tecnica-index.component';
import { ZonaTecnicaAddComponent } from './zonaTecnica/zona-tecnica-add/zona-tecnica-add.component';
import { ZonaTecnicaEditComponent } from './zonaTecnica/zona-tecnica-edit/zona-tecnica-edit.component';
import { TipocomponenteIndexComponent } from './tipocomponente/tipocomponente-index/tipocomponente-index.component';
import { TipocomponenteAddComponent } from './tipocomponente/tipocomponente-add/tipocomponente-add.component';
import { TipocomponenteEditComponent } from './tipocomponente/tipocomponente-edit/tipocomponente-edit.component';
import { TipocontratoIndexComponent } from './tipocontrato/tipocontrato-index/tipocontrato-index.component';
import { TipocontratoAddComponent } from './tipocontrato/tipocontrato-add/tipocontrato-add.component';
import { TipocontratoEditComponent } from './tipocontrato/tipocontrato-edit/tipocontrato-edit.component';


import { LogoutComponent } from './logout/logout.component';
import { PublicGuard, ProtectedGuard } from 'ngx-auth';
import { TipocomponenteAdicionalIndexComponent } from './tipocomponente/tipocomponente-adicional-index/tipocomponente-adicional-index.component';
import { Candeactivateguard } from './shared/can-deactivate-guard/can-deactivate-guard.service';
import { AuthGuard } from './shared/_guards';
import { UsuariosPerfilComponent } from './pages/usuarios/usuarios-perfil/usuarios-perfil.component';

const appRoutes: Routes = [

  // Site routes goes here
  {
    path: '',
    component: SiteLayoutComponent,
    canActivate: [ProtectedGuard],
    canActivateChild: [ AuthGuard ],
    children: [
      { path: '', component: HomeComponent, pathMatch: 'full' },
      { path: 'usuarios', component: UsuariosIndexComponent},
      { path: 'usuarios/add', component: UsuariosCreateComponent , canDeactivate: [Candeactivateguard] },
      { path: 'usuarios/edit/:id', component: UsuariosEditComponent , canDeactivate: [Candeactivateguard] },
      { path: 'perfil', component: UsuariosPerfilComponent , canDeactivate: [Candeactivateguard] },
      { path: 'empresas', component: EmpresaIndexComponent },
      { path: 'empresas/add', component: EmpresaAddComponent , canDeactivate: [Candeactivateguard] },
      { path: 'empresas/edit/:id', component: EmpresaEditComponent , canDeactivate: [Candeactivateguard] },
      { path: 'clientes', component: ClienteIndexComponent },
      { path: 'clientes/add', component: ClienteAddComponent},
      { path: 'clientes/edit/:id', component: ClienteEditComponent},
      { path: 'zonatecnica', component: ZontaTecnicaIndexComponent},
      { path: 'zonatecnica/add', component: ZonaTecnicaAddComponent , canDeactivate: [Candeactivateguard] },
      { path: 'zonatecnica/edit/:id', component: ZonaTecnicaEditComponent , canDeactivate: [Candeactivateguard]},
      { path: 'tipocomponente', component: TipocomponenteIndexComponent},
      { path: 'tipocomponenteadicional', component: TipocomponenteAdicionalIndexComponent  },
      { path: 'tipocomponente/add/:tipo', component: TipocomponenteAddComponent  , canDeactivate: [Candeactivateguard]},
      { path: 'tipocomponente/edit/:id', component: TipocomponenteEditComponent , canDeactivate: [Candeactivateguard]},
      { path: 'tipocontrato', component: TipocontratoIndexComponent },
      { path: 'tipocontrato/add', component: TipocontratoAddComponent  , canDeactivate: [Candeactivateguard]},
      { path: 'tipocontrato/edit/:id', component: TipocontratoEditComponent  , canDeactivate: [Candeactivateguard]},
      { path: 'estacionservicio', loadChildren: './estacionservicio/estacionservicio.module#EstacionservicioModule'},
      { path: 'contrato', loadChildren: './contratos/contratos.module#ContratosModule' },
      { path: 'incidencia', loadChildren: './incidencias/incidencias.module#IncidenciasModule'},
      { path: 'reportes', loadChildren: './reportes/reportes.module#ReportesModule' },
      { path: 'menuperfil', loadChildren: './pages/menuperfil/menuperfil.module#MenuperfilModule' },
      { path: 'perfiles', loadChildren: './pages/perfil/perfil.module#PerfilModule' },
      { path: 'obra/fichacomercial', loadChildren: './pages/obra/fichacomercial/fichacomercial.module#FichacomercialModule' },
      { path: 'obra/formulariodinamico', loadChildren: './pages/obra/fomulariodinamico/fomulariodinamico.module#FomulariodinamicoModule' },



    ]

  },

  // no layout routes
  { path: 'login', canActivate: [PublicGuard], component: LoginComponent },
  { path: 'login/:id', canActivate: [PublicGuard], component: LoginComponent },
  { path: 'logout', canActivate: [ProtectedGuard], component: LogoutComponent },
  // otherwise redirect to home
  { path: '**', redirectTo: '' }
];
@NgModule({
  imports: [RouterModule.forRoot(appRoutes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }



