import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { EmpresaService } from "./../empresa.service";
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from './../../shared';
import { empresa } from '../../viewmodel/empresa';
@Component({
  selector: 'app-empresa-add',
  templateUrl: './empresa-add.component.html',
  styleUrls: ['./empresa-add.component.css']
})
export class EmpresaAddComponent implements OnInit {

  empresadata: empresa;
  constructor(
    private spinnerService: Ng4LoadingSpinnerService,
    private empresaService: EmpresaService,
    private router: Router,
    private notificationService: NotificationService

  ) { }


  ngOnInit() {
    this.empresadata= new empresa();
  }

  add() {
    this.spinnerService.show();
   
   
    this.empresaService.add(this.empresadata)
      .subscribe((value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess('Empresa agregada.')
        this.router.navigateByUrl('empresas');
      },(err)=>{
       this.spinnerService.hide();
       if(err.status_code==422)
       {
        this.notificationService.onError("La empresa ya existe");
       }
       else{ this.notificationService.onError("Se ha producido un error.");}
      });
  }

}



