import { Component, OnInit } from '@angular/core';
import { Router , ActivatedRoute } from '@angular/router';
import { EmpresaService } from "./../empresa.service";
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from './../../shared';
import { empresa } from '../../viewmodel/empresa';

@Component({
  selector: 'app-empresa-edit',
  templateUrl: './empresa-edit.component.html',
  styleUrls: ['./empresa-edit.component.css']
})
export class EmpresaEditComponent implements OnInit {

  constructor(
    private spinnerService: Ng4LoadingSpinnerService,
    private empresaService: EmpresaService,
    private router: Router,
    private route: ActivatedRoute,
    private notificationService: NotificationService
  ) { }
  empresadata:empresa;
  ngOnInit() {
    this.empresadata= new empresa();
    this.getById();
  }

  getById(){
    this.spinnerService.show();
  	const $id = this.route.snapshot.paramMap.get('id');
    this.empresaService.getById($id)
      .subscribe((response) => {
        this.empresadata = response.data
        this.spinnerService.hide();
    })
  }
  update() {
    this.spinnerService.show();
  	this.empresaService.update(this.empresadata)
      .subscribe((value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess('Empresa Actualizada con Exito.')
        this.router.navigate(['empresas']);   
      });
  }

}



