import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ZonaTecnicaService } from "./../zona-tecnica.service";
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from './../../shared';
import { zonatecnica } from '../../viewmodel/zonatecnica';


@Component({
  selector: 'app-zona-tecnica-edit',
  templateUrl: './zona-tecnica-edit.component.html',
  styleUrls: ['./zona-tecnica-edit.component.css']
})
export class ZonaTecnicaEditComponent implements OnInit {
  item: zonatecnica;
  constructor(private spinnerService: Ng4LoadingSpinnerService,
    private zonaTecnicaService: ZonaTecnicaService,
    private router: Router,
    private route: ActivatedRoute,
    private notificationService: NotificationService) { }



  ngOnInit() {
    this.item = new zonatecnica();
    this.getById();
  }

  getById() {
    this.spinnerService.show();
    const $id = this.route.snapshot.paramMap.get('id');
    this.zonaTecnicaService.getById($id)
      .subscribe((response) => {
        this.item = response.data
        this.spinnerService.hide();
      })
  }
  update() {
    this.spinnerService.show();
    this.zonaTecnicaService.update(this.item)
      .subscribe((value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess('Zona Tecnica Actualziada con Exito.')
        this.router.navigate(['zonatecnica']);
      });
  }
}



