import { Injectable } from '@angular/core';
import { Observable } from "rxjs";
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { EnvironmentService } from "./../shared/environment/environment.service";
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';

@Injectable()
export class TipocompoenenteService {

  constructor(private _http: HttpClient,
    private environmentService: EnvironmentService) { }

  public get(value) {
    let url = this.environmentService.setApiServiceWithVars('tipocomponente', 'tipo='+value)
    return this._http.get(url)
      .map(res => res)
      .catch(this.handleError)
  }


  add(userData) {
    let url = this.environmentService.setApiService('tipocomponente')
    return this._http.post(url, userData)
      .map(res => res)
      .catch(this.handleError)
  }

 
  getById($id) {
    let url = this.environmentService.setApiServiceById('tipocomponente', $id)
    return this._http.get(url)
      .map(res => res)
      .catch(this.handleError)
  }

  update(userData) {
    let url = this.environmentService.setApiServiceById('tipocomponente', userData.id)
    return this._http.put(url, userData)
      .map(res => res)
      .catch(this.handleError)
  }
  delete($id) {
    let url = this.environmentService.setApiServiceById('tipocomponente', $id)
    return this._http.delete(url)
      .map(res => res)
      .catch(this.handleError)
  }
 

  private handleError(error: HttpErrorResponse | any) {
    let errMsg: any;
    errMsg = error;
    console.error(errMsg);
    return Observable.throw(errMsg);

  }

}



