import { Injectable } from '@angular/core';
import { Observable } from "rxjs";
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { EnvironmentService } from "./../shared/environment/environment.service";
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';

@Injectable()
export class TipocontratoService {

  constructor(private _http: HttpClient,
    private environmentService: EnvironmentService) { }

  public get(page) {
    let url = this.environmentService.setApiServiceWithPage('tipocontrato', page)
    return this._http.get(url)
      .map(res => res)
      .catch(this.handleError)
  }


  add(userData) {
    let url = this.environmentService.setApiService('tipocontrato')
    return this._http.post(url, userData)
      .map(res => res)
      .catch(this.handleError)
  }
  
  getById($id) {
    let url = this.environmentService.setApiServiceById('tipocontrato', $id)
    return this._http.get(url)
      .map(res => res)
      .catch(this.handleError)
  }

  update(userData) {
    let url = this.environmentService.setApiServiceById('tipocontrato', userData.id)
    return this._http.put(url, userData)
      .map(res => res)
      .catch(this.handleError)
  }
  delete($id) {
    let url = this.environmentService.setApiServiceById('tipocontrato', $id)
    return this._http.delete(url)
      .map(res => res)
      .catch(this.handleError)
  }
 

  private handleError(error: HttpErrorResponse | any) {
    let errMsg: string;
    errMsg = error.error
    console.error(errMsg);
    return Observable.throw(errMsg);

  }


}
 


