import { Injectable } from '@angular/core';
import { Observable } from "rxjs";
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { EnvironmentService } from './../shared/environment/environment.service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';


@Injectable()
export class EstacionservicioService {


  constructor(private _http: HttpClient,
    private environmentService: EnvironmentService) { }

  public get(page) {
    let url = this.environmentService.setApiServiceWithPageEnterprise('estacionservicio', page)
    return this._http.get(url)
      .map(res => res)
      .catch(this.handleError);
  }
  public getbyobra(page, obra) {
    let url = this.environmentService.setApiServiceWithPageEnterprise('estacionservicio', page)
    if (obra != undefined) {
      url += '&obra=1';
    }

    return this._http.get(url)
      .map(res => res)
      .catch(this.handleError);
  }

  public getbycliente(empresa, cliente) {
    let url = this.environmentService.setApiServiceWithVars('estacionservicio', 'empresa=' + empresa + '&cliente=' + cliente);
    return this._http.get(url)
      .map(res => res)
      .catch(this.handleError)
  }
  public getactbycliente(empresa, cliente) {
    let url = this.environmentService.setApiServiceWithVars('estacionservicio', 'empresa=' + empresa + '&cliente=' + cliente + '&activo=1');
    return this._http.get(url)
      .map(res => res)
      .catch(this.handleError)
  }


  add(userData) {
    let url = this.environmentService.setApiService('estacionservicio')
    return this._http.post(url, userData)
      .map(res => res)
      .catch(this.handleError)
  }


  getById($id) {
    let url = this.environmentService.setApiServiceById('estacionservicio', $id)
    return this._http.get(url)
      .map(res => res)
      .catch(this.handleError)
  }

  update(userData) {
    let url = this.environmentService.setApiServiceById('estacionservicio', userData.id)
    return this._http.put(url, userData)
      .map(res => res)
      .catch(this.handleError)
  }

  updatecomponent(userData) {
    let url = this.environmentService.setApiService('updatecomponent');
    return this._http.post(url, userData)
      .map(res => res)
      .catch(this.handleError)
  }
  delete($id) {
    let url = this.environmentService.setApiServiceById('estacionservicio', $id)
    return this._http.delete(url)
      .map(res => res)
      .catch(this.handleError)
  }

 /*  deleteComponent($id) {
    let url = this.environmentService.setApiServiceById('componente', $id)
    return this._http.delete(url)
      .map(res => res)
      .catch(this.handleError)
  } */

  private handleError(error: HttpErrorResponse | any) {
    let errMsg: string;
    errMsg = error.error
    console.error(errMsg);
    return Observable.throw(errMsg);

  }
}



