import { Component, OnInit } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from './../../shared';
import { LocalDataService } from './../../shared';
import { ClienteService } from "./../cliente.service";
declare var $;

import "datatables.net";
import "datatables.net-bs4";
import "datatables.net-buttons";
@Component({
  selector: 'app-cliente-index',
  templateUrl: './cliente-index.component.html',
  styleUrls: ['./cliente-index.component.css']
})
export class ClienteIndexComponent implements OnInit {
  clientes = [];
  table = null;
  constructor( private spinnerService: Ng4LoadingSpinnerService,
    private clienteService: ClienteService,
    private localDataService: LocalDataService,
    private notificationService: NotificationService
  ) { }

  ngOnInit() {
    this.get();

  }
  get() {
    let empresa=this.localDataService.getLocalData('empresaSeleccionada').id;
    this.spinnerService.show();
    this.clienteService.get('1&empresa='+empresa)
      .subscribe((response) => {
        this.clientes = response.data;
        this.reInitDatatable();
        this.spinnerService.hide();

      });
  }
  loadtable() {
;
    let exampleId: any = $('#example23');    
  
    this.table=exampleId.DataTable({
      dom: 'Bfrtip',
      buttons: [
        'copy', 'csv', 'excel', 'pdf', 'print'
    ]
    });
  }
  ngAfterViewInit() {

   

  }
  delete(id) {
    this.spinnerService.show();
    this.clienteService.delete(id)
      .subscribe((value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess('Empresa Eliminada con Exito.')
        this.get();

      });
  }
  private reInitDatatable(): void {
    if (this.table) {
      this.table.destroy()
      this.table = null
    }
    setTimeout(() => this.loadtable(), 0)
  }

}



