import { Component, OnInit } from '@angular/core';
import { LocalDataService, NotificationService } from '../../../shared';
import { UsuariosService } from '../../../usuarios/usuarios.service';
import { user } from '../../../viewmodel/user';
import { EmpresaService } from '../../../empresa/empresa.service';
import { FormControl, Validators, FormGroup } from '@angular/forms';
import { CustomValidators } from 'ng2-validation';

@Component({
  selector: 'app-usuarios-perfil',
  templateUrl: './usuarios-perfil.component.html',
  styleUrls: ['./usuarios-perfil.component.css']
})
export class UsuariosPerfilComponent implements OnInit {
  user: any;
  userdata: user;
  empresas: any;
  myForm: FormGroup;
  constructor(private localDataService: LocalDataService,
    private usuarioServcice: UsuariosService,
    private empresaService: EmpresaService,
    private notificationService: NotificationService,
  ) {
    this.userdata = new user();
    this.getUserById();
  }

  ngOnInit() {
    const password = new FormControl('', Validators.required);
    const email = new FormControl('', [Validators.required, Validators.email]);
    const rpassword = new FormControl('', [Validators.required, CustomValidators.equalTo(password)]);

    this.myForm = new FormGroup({
      email: email,
      password: password,
      rpassword: rpassword
    });
  }
  getUserById() {
    this.user = this.localDataService.getLocalData('userInfo');

    this.usuarioServcice.getUserById(this.user.id)
      .subscribe((response) => {
        this.userdata = response.data;
        this.getempresas();
      });
  }
  getPerfil(idperfil) {
    if (idperfil == "1") { return "Tecnico"; }
    else if (idperfil == "2") { return "Administrador"; }
    else if (idperfil == "4") { return "Administrativo"; }
    else { return "Supervisor"; }

  }
  getempresas() {
    this.empresaService.get(1).subscribe((value) => {
      this.empresas = value.data;

    });

  }
  updateUser() {


    this.usuarioServcice.updateUser(this.userdata)
      .subscribe((value) => {

        this.notificationService.onSuccess('Usuario Actualizado con Exito.')

      });
  }

  changepassword() {

    this.usuarioServcice.cambiarpassword(this.userdata)
      .subscribe((value) => {
        this.notificationService.onSuccess('Se cambiado la contraseña con exito.');


      }, (err) => {

        if (err.status_code == 422) {
          this.notificationService.onError("Error en el proceso");
        }
        else { this.notificationService.onError("Se ha producido un error."); }
      });
  }

}



