import { Component, OnInit, EventEmitter, Input, Output } from '@angular/core';
import { Dia } from '../viewmodel/dia';
import { Subscription } from 'rxjs';
import { group } from '@angular/core/src/animation/dsl';
import { of } from 'rxjs/observable/of';
@Component({
  selector: 'app-dias-week',
  templateUrl: './dias-week.component.html',
  styleUrls: ['./dias-week.component.css']
})
export class DiasWeekComponent implements OnInit {
  diasweek: Dia[];
  @Output() onvaluechanged = new EventEmitter<any>();


  @Input() diasresult: string;
  @Input() index: any;
  constructor() {

  }

  ngOnInit() {
    this.diasweek = [{ name: 'LUN', select: false },
    { name: 'MAR', select: false },
    { name: 'MIE', select: false },
    { name: 'JUE', select: false },
    { name: 'VIE', select: false },
    { name: 'SAB', select: false },
    { name: 'DOM', select: false },
    { name: 'FEST', select: false },
    ];
    if (this.diasresult != null && this.diasresult != '') {
      let arrresul = this.diasresult.split(',');
      for (let i = 0; i <= arrresul.length; i++) {
        for (let j = 0; j < this.diasweek.length; j++) {
          if (arrresul[i] == this.diasweek[j].name) {
            this.diasweek[j].select = true;
          }
        }

      }

    }


  }
  seleccionardia(item) {
    if (item.select) {
      item.select = false;
    } else {
      item.select = true;
    }
    let result;
    result = this.diasweek.filter(comp => comp.select);
    this.diasresult = result.map(o => o.name).join();
    this.onvaluechanged.emit({ dias: this.diasresult, index: this.index });
  }
  valuechanged(value) {
    this.onvaluechanged.emit(this.diasresult);
  }
  clearlist() {
    this.diasweek = [{ name: 'LUN', select: false },
    { name: 'MAR', select: false },
    { name: 'MIE', select: false },
    { name: 'JUE', select: false },
    { name: 'VIE', select: false },
    { name: 'SAB', select: false },
    { name: 'DOM', select: false },
    { name: 'FEST', select: false },
    ];
  }

}



