import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { UsuariosService } from "./../usuarios.service";
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from './../../shared';

import { user } from '../../viewmodel/user';
import { EmpresaService } from '../../empresa/empresa.service';
import { ZonaTecnicaService } from '../../zonaTecnica/zona-tecnica.service';
import { Candeactivateguard } from '../../shared/can-deactivate-guard/can-deactivate-guard.service';
import { PerfilService } from '@app/services/perfil/perfil.service';
import { SwalComponent } from '@toverux/ngx-sweetalert2';

@Component({
  selector: 'app-usuarios-create',
  templateUrl: './usuarios-create.component.html',
  styleUrls: ['./usuarios-create.component.css']
})
export class UsuariosCreateComponent implements OnInit {
  userdata: user;
  empresas: any;
  zonastecnicas: any;
  perfiles: any;
  user_names: string;

  @ViewChild('supervisorSwal') private supervisorSwal: SwalComponent;

  constructor(
    private spinnerService: Ng4LoadingSpinnerService,
    private usuarioServcice: UsuariosService,
    private router: Router,
    private notificationService: NotificationService,
    private empresaService: EmpresaService,
    private zonaTecnicaService: ZonaTecnicaService,
    private candeactivateguard: Candeactivateguard,
    private perfilService: PerfilService
  ) {
    this.candeactivateguard.setedit(true);
  }
  allTechnologies = [
    'Java',
    'Angular',
    'Hibernate',
    'Spring'
  ];
  list = '';
  ngOnInit() {
    this.spinnerService.show();
    this.userdata = new user();
    this.getempresas();
    this.getZonasTecnicas();
    this.getPerfiles();
  }

  addUsuario() {
    this.candeactivateguard.setedit(false);
    this.spinnerService.show();

    this.userdata.empresas = this.userdata.empresas ? this.userdata.empresas.join() : "";
    this.userdata.zonastecnicas = this.userdata.zonastecnicas ? this.userdata.zonastecnicas.join() : '';
    
    this.usuarioServcice.addUser(this.userdata)
      .subscribe((value) => {
        this.spinnerService.hide();

        if(value.data.full) {
          this.user_names = value.data.list.map(item => item.name).join(', ');
          this.userdata.empresas = this.userdata.empresas.split(",");
          this.userdata.zonastecnicas = this.userdata.zonastecnicas.split(",");

          this.supervisorSwal.show();
        } else {
          this.notificationService.onSuccess('Usuario agregado.')
          this.router.navigateByUrl('usuarios');
        }
       
      }, (err) => {
        this.spinnerService.hide();
        this.userdata.empresas = this.userdata.empresas.split(",");
        this.userdata.zonastecnicas = this.userdata.zonastecnicas.split(",");

        if (err.status_code == 422) {
          this.notificationService.onError("El usuario ya existe");
        }
        else { this.notificationService.onError("Se ha producido un error."); }
      });
  }

  getempresas() {
    this.empresaService.get(1).subscribe((value) => {
      this.empresas = value.data;

    });

  }
  getPerfiles(){
    this.perfilService.get(1).subscribe((value) => {
      this.perfiles = value.data;

    });

  }
  getZonasTecnicas() {
    this.zonaTecnicaService.get(1).subscribe((value) => {
      this.zonastecnicas = value.data;
      this.spinnerService.hide();
    });
  }

}



