import { Injectable } from '@angular/core';
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, CanActivateChild } from '@angular/router';
import { LocalDataService } from '../localData';



@Injectable()
export class AuthGuard implements CanActivateChild {
    constructor(
        private router: Router,
        private localDataService: LocalDataService
    ) { }


    canActivateChild(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {
        if (route.routeConfig.path != '' && state.url != '/perfil'&& state.url != '/' && !state.url.includes('auditoria')) {

            const menu = this.localDataService.getmenu();
            let result;
            for (let i = 0; i <
                menu.length; i++) {
                result = menu[i].opciones.filter(mn => mn.ruta != null && mn.ruta != '/' && state.url.includes(mn.ruta));
                if (result.length > 0) {
                    result = true;
                    break;
                } else { result = false; }
            }


            if (!result) {
                this.router.navigateByUrl('/');
                return false;
            }
            else { return true; }
        } else {
            return true;
        }


    }
}


