import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { LoginService } from "./login.service";
import { AuthenticationService } from '../shared';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { FormValidationService } from "../shared";
import { NotificationService } from '../shared';
import { UsuariosService } from '../usuarios/usuarios.service';
import { FormGroup, Validators, FormBuilder, FormControl } from '@angular/forms';
import { CustomValidators } from 'ng2-validation';
import { environment } from '../../environments/environment';
declare var $;

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  loginData = { username: '', password: '', rpassword: '', email: '',remember_token:'' }
  message = '';
  errMsgArr = [];
  myForm: FormGroup;
  apiurl: any;
  constructor(private spinnerService: Ng4LoadingSpinnerService,
    private loginService: LoginService,
    private authService: AuthenticationService,
    private router: Router,
    private formValidationService: FormValidationService,
    private notificationService: NotificationService,
    private usuarioService: UsuariosService,
    private route: ActivatedRoute,
    private formBuilder: FormBuilder) {

    this.apiurl = environment.apiUrl;
    const password = new FormControl('', Validators.required);
    const email = new FormControl('', [Validators.required, Validators.email]);
    const rpassword = new FormControl('', [Validators.required, CustomValidators.equalTo(password)]);

    this.myForm = new FormGroup({
      email: email,
      password: password,
      rpassword: rpassword
    });

  }

  ngOnInit() {
    const $id = this.route.snapshot.paramMap.get('id');
    if ($id != undefined && $id != null) {
      $("#loginform").slideUp();
      $("#changepasswordform").fadeIn();
      $("#recoverform").slideUp();
      this.loginData.remember_token=$id;
    }
  }

  onClickMe() {
    this.router.navigate(["/"]);
  }

  public login() {
    this.spinnerService.show();
    this.authService.login(this.loginData).subscribe((value) => {
      this.loginService.getMe().subscribe((value) => {
        localStorage.setItem('userInfo', JSON.stringify(value.data))
        console.log(JSON.parse(localStorage.getItem('userInfo')).name);
        this.spinnerService.hide();
        this.notificationService.onSuccess('Bienvenido...' + JSON.parse(localStorage.getItem('userInfo')).name)
        this.router.navigateByUrl('/');
      });
    }, err => {
      this.spinnerService.hide();
      if (err.status_code == 422) {
        this.errMsgArr = this.formValidationService.getErrors(err.errors);
      } else {
        this.errMsgArr = [err.error.message]
      }
    });
  }
  ngAfterViewInit() { this.initpage(); }
  initpage() {
    $(function () {
      $(".preloader").fadeOut();
    });

    // ============================================================== 
    // Login and Recover Password 
    // ============================================================== 
    $('#to-recover').on("click", function () {
      $("#loginform").slideUp();
      $("#changepasswordform").slideUp();
      $("#recoverform").fadeIn();
    });
    /*back login */
    $('#to-login').on("click", function () {
      $("#recoverform").slideUp();
      $("#changepasswordform").slideUp();
      $("#loginform").fadeIn();
    });
  }

  recordarcontrasena() {
    this.spinnerService.show();

    this.usuarioService.recordar(this.loginData)
      .subscribe((value) => {
        this.notificationService.onSuccess('Se enviado un email al correo registrado.');
        this.router.navigateByUrl('/');
        this.spinnerService.hide();

      }, (err) => {
        this.spinnerService.hide();
        if (err.status_code == 422) {
          this.notificationService.onError("Error en el proceso");
        }
        else { this.notificationService.onError("Se ha producido un error."); }
      });
  }
   
  changepassword() {  
    this.spinnerService.show();

    this.usuarioService.recordar(this.loginData)
      .subscribe((value) => {
        this.notificationService.onSuccess('Se cambiado la contraseña con exito.');
        this.router.navigateByUrl('/');
        this.spinnerService.hide();
 
      }, (err) => {
        this.spinnerService.hide();
        if (err.status_code == 422) {
          this.notificationService.onError("Error en el proceso");
        }
        else { this.notificationService.onError("Se ha producido un error."); }
      });
  }

}



