import { Component, OnInit, AfterViewInit, ViewChild, OnDestroy } from '@angular/core';
declare var $;

import { DashboardService } from '../dashboard/dashboard.service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/takeWhile';
import { ESTADOINCIDENCIAS } from '../mocks/estadoincidente';
import { LocalDataService } from '@app/shared';
import { timer } from 'rxjs/observable/timer';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit, AfterViewInit, OnDestroy {

  @ViewChild('gmap') gmapElement: any;
  map: google.maps.Map;
  tipoinicio = '0';
  constructor(private dashboardService: DashboardService,
    private spinnerService: Ng4LoadingSpinnerService,
    private localdataservice: LocalDataService, ) {
    this.tipoinicio = this.localdataservice.getinicio();
  }
  dashboaditem: any;
  alive = true;
  initmap = true;
  lat = 40.50017;
  long = -3.89239;
  markers = [];
  usuario: any;
  estadoincidente = ESTADOINCIDENCIAS;
  formulacalculo = "";

  ngOnInit() {
    this.dashboaditem = {
      pendientes: 0,
      planificados: 0,
      resueltos: 0,
      tecnicossinaviso: 0,
      porcEfectividad: 0,
      tecnicos: [],
      dateformat: '',
      totalmonth: '',
      efecporcentaje: '',
      resueltomes: ''
    };
    this.usuario = JSON.parse(localStorage.getItem('userInfo'));
    this.get();
    timer(0, 40000)
      .takeWhile(() => this.alive) // only fires when component is alive
      .subscribe(() => {
        this.get();
      });

  }
  get() {


    this.spinnerService.show();
    this.dashboardService.get(0)
      .subscribe((response) => {

        this.dashboaditem = response.data;
        if (this.usuario.perfil == 2
          || this.usuario.perfil == 3
          || this.usuario.perfil == 4) {
          this.createMap(this.dashboaditem.tecnicos);

        }

        this.spinnerService.hide();

      });
  }
  setMapOnAll(map) {
    for (let i = 0; i < this.markers.length; i++) {
      this.markers[i].setMap(map);
    }
    this.markers = [];
  }
  createMap(marks) {

    let myLatlng = new google.maps.LatLng(this.lat, this.long);
    if (this.initmap) {
      let mapProp = {
        center: myLatlng,
        zoom: 10,
        mapTypeId: google.maps.MapTypeId.ROADMAP,

      };
      this.map = new google.maps.Map(this.gmapElement.nativeElement, mapProp);
      this.initmap = false;
    }

    this.setMapOnAll(null);
    let indexpos = Math.floor((Math.random() * marks.length));
    for (let i = 0; i < marks.length; i++) {
      if (indexpos == i) {
        myLatlng = new google.maps.LatLng(marks[i].lat, marks[i].lng);
      }


      let marker = new google.maps.Marker({
        position: new google.maps.LatLng(marks[i].lat, marks[i].lng),
        map: this.map,
        animation: google.maps.Animation.DROP,
        title: 'Hola!',
        label: marks[i].name.substring(0, 1)
      });
      let contentString = '<div class="info-window-content"><h2>' + marks[i].name + '</h2>' +
        '<h3>Cliente:' + (marks[i].ult_estacionservicio == null ? '' : marks[i].ult_estacionservicio.name) + '</h3>' +
        '<p> ' + (marks[i].phone == null ? '' : marks[i].phone) + '<br> ' + marks[i].email + '</p></div>';

      let infowindow = new google.maps.InfoWindow({
        content: contentString
      });

      google.maps.event.addListener(marker, 'click', function () {
        infowindow.open(this.map, marker);
      });
      this.markers.push(marker);
    }
    this.map.setCenter(myLatlng);



  }
  ngAfterViewInit() {
    $(".preloader").fadeOut();
    //$.getScript('assets/dist/js/pages/mapscustom.js');

    $.getScript('assets/dist/js/dashboard1.js');
  }
  ngOnDestroy() {
    this.alive = false; // switches your IntervalObservable off
  }
  getclassestadoincidencia(estado) {
    let result;
    result = this.estadoincidente.filter(estadoinc => estadoinc.value === estado);
    return result.length > 0 ? result[0].btn : '';
  }

}




