import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { TipocontratoService } from "./../tipocontrato.service";
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from './../../shared';
import { LocalDataService } from './../../shared';
import { tipocomponente } from '../../viewmodel/tipocomponente';
import { CategoriaIncidente } from '../../viewmodel/categoriaincidente';
import { tipocontrato } from '../../viewmodel/tipocontrato';
import {ViewChild, ElementRef} from '@angular/core';

@Component({
  selector: 'app-tipocontrato-add',
  templateUrl: './tipocontrato-add.component.html',
  styleUrls: ['./tipocontrato-add.component.css']
})
export class TipocontratoAddComponent implements OnInit {

  item: tipocontrato;
  newcategoriaincidente: CategoriaIncidente;
 
  constructor(
    private spinnerService: Ng4LoadingSpinnerService,
    private tipocontratoService: TipocontratoService,
    private router: Router,
    private notificationService: NotificationService,
    private localDataService: LocalDataService

  ) { }


  ngOnInit() {
    this.item = new tipocontrato();
    this.item.categoriaincidentes = [];
    this.newcategoriaincidente = new CategoriaIncidente();
    this.newcategoriaincidente.dias = "";
  }

  add() {

    this.spinnerService.show();


    this.tipocontratoService.add(this.item)
      .subscribe((value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess('Tipo Contrato agregado.')
        this.router.navigateByUrl('tipocontrato');
      }, (err) => {
        this.spinnerService.hide();
        if (err.status_code == 422) {
          this.notificationService.onError("El Tipo Contrato ya existe");
        }
        else { 
          this.notificationService.onError("Se ha producido un error."+err.errors.name[0]); }
      });
  }

  onvaluechanged(value) {
    if (value.index === undefined || value.index === '') {
      this.newcategoriaincidente.dias = value.dias;
    }else {
      this.item.categoriaincidentes[value.index].dias = value.dias;
    }
  
  }
  addNewcategoria(ltsdias) {
    this.item.categoriaincidentes.push(this.newcategoriaincidente);
    this.newcategoriaincidente = new CategoriaIncidente();
    this.newcategoriaincidente.dias = "";
    ltsdias.clearlist();
  }
  onradioChange(row,value) {
    row.tipotiempo=value;
  }
  deletecomponent(componente) {
    let index = this.item.categoriaincidentes.indexOf(componente);
    this.item.categoriaincidentes.splice(index, 1);

  }
} 



