import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ZonaTecnicaService } from "./../zona-tecnica.service";
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from './../../shared';
import { LocalDataService } from './../../shared';
import { zonatecnica } from '../../viewmodel/zonatecnica';
import { Candeactivateguard } from '../../shared/can-deactivate-guard/can-deactivate-guard.service';


@Component({
  selector: 'app-zona-tecnica-add',
  templateUrl: './zona-tecnica-add.component.html',
  styleUrls: ['./zona-tecnica-add.component.css']
})
export class ZonaTecnicaAddComponent implements OnInit {
  item:zonatecnica;
  constructor(
    private spinnerService: Ng4LoadingSpinnerService,
    private zonaTecnicaService: ZonaTecnicaService,
    private router: Router,
    private notificationService: NotificationService,
    private localDataService:LocalDataService,
    private candeactivateguard: Candeactivateguard,
  ) { 
    this.candeactivateguard.setedit(true);
  }


  ngOnInit() {
    this.item= new zonatecnica();
  }

  add() {
    this.spinnerService.show();
    this.candeactivateguard.setedit(false);
   this.item.fk_empresa=this.localDataService.getLocalData('empresaSeleccionada').id;
   this.item.user_id=this.localDataService.getLocalData('userInfo').id;
    this.zonaTecnicaService.add(this.item)
      .subscribe((value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess('Zona Tecnica agregada.')
        this.router.navigateByUrl('zonatecnica');
      },(err)=>{
       this.spinnerService.hide();
       if(err.status_code==422)
       {
        this.notificationService.onError("La zona tecnica ya existe");
       }
       else{ this.notificationService.onError("Se ha producido un error.");}
      });
  }


}
 


