import { Injectable } from '@angular/core';
import { Observable } from "rxjs";
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { EnvironmentService } from "./../shared/environment/environment.service";
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
@Injectable()
export class UsuariosService {

  constructor(private _http: HttpClient,
    private environmentService: EnvironmentService) { }

  public getUsers(page) {
    let url = this.environmentService.setApiServiceWithPage('users', page)
    return this._http.get(url)
      .map(res => res)
      .catch(this.handleError)
  }


  addUser(userData) {
    let url = this.environmentService.setApiService('register')
    return this._http.post(url, userData)
      .map(res => res)
      .catch(this.handleError)
  }


  getUserById($id) {
    let url = this.environmentService.setApiServiceById('usuario/getbyid', $id)
    return this._http.get(url)
      .map(res => res)
      .catch(this.handleError)
  }

  updateUser(userData){
    let url = this.environmentService.setApiServiceById('usuario/update', userData.id)
    return this._http.put(url, userData)
        .map(res=> res)
        .catch(this.handleError)
  }
  deleteUser($id){
    let url = this.environmentService.setApiServiceById('usuario/delete', $id)
    return this._http.delete(url)
        .map(res=> res)
        .catch(this.handleError)
  }
  cambiarpassword(userData) {
    let url = this.environmentService.setApiServiceById('cambiarpassword', userData.id)
    return this._http.put(url, userData)
      .map(res => res)
      .catch(this.handleError)
  }

  recordar(userData) {
    let url = this.environmentService.setApiService('recordarcontrasena');
    return this._http.post(url, userData)
      .map(res => res)
      .catch(this.handleError);
  }


  private handleError(error: HttpErrorResponse | any) {
    let errMsg: string;
    errMsg = error.error
    console.error(errMsg);
    return Observable.throw(errMsg);
  }

}



