import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { TipocompoenenteService } from "./../tipocompoenente.service";
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from './../../shared';
import { tipocomponente } from '../../viewmodel/tipocomponente';
import { tipocomponentefield } from '../../viewmodel/tipocomponentefield';
import { tipocomponenteadicional } from '../../viewmodel/tipocomponenteadicional';
import { isNgTemplate } from '@angular/compiler';
@Component({
  selector: 'app-tipocomponente-edit',
  templateUrl: './tipocomponente-edit.component.html',
  styleUrls: ['./tipocomponente-edit.component.css']
})
export class TipocomponenteEditComponent implements OnInit {
  item:tipocomponente;
  itemcomponente:tipocomponentefield;
  listaitem = [];
  idadicional='-1';

  constructor(private spinnerService: Ng4LoadingSpinnerService,
    private tipocompoenenteService: TipocompoenenteService,
    private router: Router,
    private route: ActivatedRoute,
    private notificationService: NotificationService) { }



  ngOnInit() {
    this.item= new tipocomponente();

    this.itemcomponente= new tipocomponentefield();
    this.itemcomponente.tipo="-1";
    this.getById();
    this.get();
  }
  get() {
      
    this.tipocompoenenteService.get(1)
      .subscribe((response) => {
        this.listaitem = response.data;
        this.spinnerService.hide();

      });
  }

  getById() {
    this.spinnerService.show();
    const $id = this.route.snapshot.paramMap.get('id');
    this.tipocompoenenteService.getById($id)
      .subscribe((response) => {
        this.item = response.data
        this.spinnerService.hide();
      })
  }
  update() {
    this.spinnerService.show();
    for(let i=0;i<this.item.componentesfields.length;i++)
    {
      this.item.componentesfields[i].orden=i;
    }
    this.tipocompoenenteService.update(this.item)
      .subscribe((value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess('Tipo Componente Actualziado con Exito.');
        if(this.item.adicional)
        {
          this.router.navigateByUrl('tipocomponenteadicional');
        }else{
          this.router.navigateByUrl('tipocomponente');
        }
      
        
      });
  }
  addfields(){

    this.item.componentesfields.push(this.itemcomponente);
    this.itemcomponente= new tipocomponentefield(); 
    this.itemcomponente.tipo="-1";
  }
  delete(row)
  {
    var index=this.item.componentesfields.indexOf(row);
    this.item.componentesfields.splice(index, 1);


  }

  deleteComponente(row)
  {
    var index=this.item.componentesadicional.indexOf(row);
    this.item.componentesadicional.splice(index, 1);


  }
  addadicional(){

    this.tipocompoenenteService.getById(this.idadicional)
      .subscribe((response) => {
        var adicional= new tipocomponenteadicional();
        adicional.fk_tipocomponenteadicional=response.data.id;
        adicional.tipocomponente=response.data;
        this.item.componentesadicional.push(adicional);
        this.idadicional='-1';
        this.spinnerService.hide();
      });
    
  }
}



