import { Injectable } from '@angular/core';
import { Observable } from "rxjs";
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { EnvironmentService } from "./../shared/environment/environment.service";
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Http, Response, RequestOptions, ResponseContentType, Headers } from '@angular/http';
@Injectable()
export class ReportesService {

  constructor(private _http: HttpClient,
    private environmentService: EnvironmentService ,
     private httpd: Http) { }

  public get(variables) {
    let url = this.environmentService.setApiServiceWithVars('reporte/componentesporcliente', variables)
    return this._http.get(url)
      .map(res => res)
      .catch(this.handleError);
  }
  consultar(ruta, userData) {
    let url = this.environmentService.setApiServiceWithPageEnterprise('reporte/' + ruta,1);
    return this._http.post(url, userData)
      .map(res => res)
      .catch(this.handleError);
  }
  download(params) {
    let aut = "Bearer " + localStorage.getItem('access_token');
    let headers = new Headers();
    headers.append('Authorization', aut);
    let options = new RequestOptions({
      headers: headers,
      responseType: ResponseContentType.Blob
    });
    let url = this.environmentService.setApiServiceWithPageEnterprise('reporte/horasSemanalesExport',1);
    url=url+'&'+params;
    return this.httpd.get(url, options)
      .map((response: Response) => <Blob>response.blob())
      .catch(this.handleError)
  }
  private handleError(error: HttpErrorResponse | any) {
    let errMsg: string;
    errMsg = error.error
    console.error(errMsg);
    return Observable.throw(errMsg);

  }

}



