import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ClienteService } from "./../cliente.service";
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from './../../shared';
import { LocalDataService } from './../../shared';
import { cliente } from '../../viewmodel/cliente';
import { Candeactivateguard } from '../../shared/can-deactivate-guard/can-deactivate-guard.service';

@Component({
  selector: 'app-cliente-add',
  templateUrl: './cliente-add.component.html',
  styleUrls: ['./cliente-add.component.css']
})
export class ClienteAddComponent implements OnInit {
  empresadata: cliente;
  constructor(
    private spinnerService: Ng4LoadingSpinnerService,
    private clienteService: ClienteService,
    private router: Router,
    private notificationService: NotificationService,
    private localDataService:LocalDataService,
    private candeactivateguard: Candeactivateguard
  ) {
    this.candeactivateguard.setedit(true);
   }


  ngOnInit() {
    this.empresadata= new cliente();
  }

  add() {
    console.log("spinner service add")
    this.spinnerService.show();
    this.candeactivateguard.setedit(false);
   this.empresadata.fk_empresa=this.localDataService.getLocalData('empresaSeleccionada').id;
   //this.empresadata.user_id=this.localDataService.getLocalData('userInfo').id;
    this.clienteService.add(this.empresadata)
      .subscribe((value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess('Cliente agregado.')
        this.router.navigateByUrl('clientes');
      },(err)=>{
       this.spinnerService.hide();
       if(err.status_code==422)
       {
        this.notificationService.onError("El cliente ya existe");
       }
       else{ this.notificationService.onError("Se ha producido un error.");}
      });
  }

}



