import { Injectable } from '@angular/core';
import { Observable } from "rxjs";
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { EnvironmentService } from './../shared/environment/environment.service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';


@Injectable()
export class IncidenciaService {

  constructor(private _http: HttpClient,
    private environmentService: EnvironmentService) { }

  public get(page) {
    let url = this.environmentService.setApiServiceWithPageEnterprise('incidencia', page)
    return this._http.get(url)
      .map(res => res)
      .catch(this.handleError)
  }


  add(userData) {
    let url = this.environmentService.setApiService('incidencia');
    return this._http.post(url, userData)
      .map(res => res)
      .catch(this.handleError);
  }


  getById($id) {
    let url = this.environmentService.setApiServiceById('incidencia', $id)
    return this._http.get(url)
      .map(res => res)
      .catch(this.handleError)
  }


  getReopenById($id) {
    let url = this.environmentService.setApiServiceById('incidencia', $id);
    url += '/edit';
    return this._http.get(url)
      .map(res => res)
      .catch(this.handleError);
  }
  public getbytoken(page) {
    let url = this.environmentService.setApiServiceWithVars('incidencia', page)
    console.log("Get by otken",url)
    return this._http.get(url)
      .map(res => res)
      .catch(this.handleError)
  }


  update(userData) {
    let url = this.environmentService.setApiServiceById('incidencia', userData.id)
    return this._http.put(url, userData)
      .map(res => res)
      .catch(this.handleError)
  }

  updatecomponent(userData) {
    let url = this.environmentService.setApiService('incidencia');
    return this._http.post(url, userData)
      .map(res => res)
      .catch(this.handleError)
  }
  delete($id) {
    let url = this.environmentService.setApiServiceById('incidencia', $id)
    return this._http.delete(url)
      .map(res => res)
      .catch(this.handleError)
  }
  
  inboxUser($id, inbox ){
    let url = this.environmentService.setApiService('incidencia') + "/inbox-user/" + $id;
    return this._http.put(url, { user_imbox_id : inbox })
      .map(res => res)
      .catch(this.handleError)
  }


  private handleError(error: HttpErrorResponse | any) {
    let errMsg: string;
    errMsg = error.error
    console.error(errMsg);
    return Observable.throw(errMsg);

  }


}



