import { Component, OnInit, AfterViewInit } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from './../../shared';
import { UsuariosService } from "./../usuarios.service";
declare var $;

import "datatables.net";
import "datatables.net-bs4";
import "datatables.net-buttons";
import { EmpresaService } from '../../empresa/empresa.service';

@Component({
  selector: 'app-usuarios-index',
  templateUrl: './usuarios-index.component.html',
  styleUrls: ['./usuarios-index.component.css']
})
export class UsuariosIndexComponent implements OnInit, AfterViewInit {
  // Our future instance of DataTable
  users = [];
  table = null;
  empresas: any;
  constructor(
    private spinnerService: Ng4LoadingSpinnerService,
    private usuariosService: UsuariosService,
    private notificationService: NotificationService,
    private empresaService: EmpresaService,
  ) { }

  ngOnInit() {
    this.getempresas();
    this.getusers();
  }
  getusers() {
    this.spinnerService.show();
    this.usuariosService.getUsers(1)
      .subscribe((response) => {
        this.users = response.data;
        this.reInitDatatable();
        this.spinnerService.hide();

      });
  }
  loadtable() {
    let exampleId: any = $('#example23');

    this.table = exampleId.DataTable({
      dom: 'Bfrtip',
      buttons: [
        'copy', 'csv', 'excel', 'pdf', 'print'
      ]
    });
  }
  private reInitDatatable(): void {
    if (this.table) {
      this.table.destroy()
      this.table = null
    }
    setTimeout(() => this.loadtable(), 0)
  }
  ngAfterViewInit() {



  }
  deleteuser(id) {
    this.spinnerService.show();
    this.usuariosService.deleteUser(id)
      .subscribe((value) => {
        this.spinnerService.hide();
        if(value.data.estado)
        {
          this.notificationService.onSuccess('Usuario Activado con Exito.');
        }
        else{
          this.notificationService.onSuccess('Usuario Eliminado con Exito.');
        }
      
        this.getusers();

      });
  }
  getPerfil(idperfil) {
    if (idperfil == "1") { return "Tecnico"; }
    else if (idperfil == "2") { return "Administrador"; }
    else if (idperfil == "4") { return "Administrativo"; }
    else { return "Supervisor"; }

  }
  getempresas() {
    this.empresaService.get(1).subscribe((value) => {
      this.empresas = value.data;

    });

  }
}    



